.contactBox {
    margin-top: 2em;
}

.child-items * {
    color: #ffffff;
    font-size: 1.1em;
    margin: 0;
    padding: 1em 2em;
}

.child-items .odd {
    background-color: rgba(255, 255, 255, 0.411);
}

.child-items table * {
    font-size: 1em;
    margin: 0;
    padding: 0.25em 1em;
}

.child-items {
    align-items: center;
}

.child-items p {
    color: rgba(255, 255, 255, 0.8);
}

.child-items h3 {
    text-decoration: underline rgb(84, 122, 218);
    font-size: 1.5em;
    font-weight: 900;
}

.child-items h2 {
    text-decoration: underline rgba(255, 166, 0, 0.938);
    font-size: 1.65em;
    font-weight: 900;
}

.child-items #inscripcion {
    background-color: #fff;
    color: #2E374D;
    transition: background-color 0.6s;
}

.child-items #inscripcion:hover {
    color: #fff;
    background-color: rgb(57, 78, 131);
}